
.header{
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    position: relative;
    background: #18181b;
    height: 100px;
}



.header li{
    display: flex;
    list-style: none;
    margin: 0px 50px 0px 0px;
    cursor: pointer;
    color: #efeff1;
}

.header a{
    color: #efeff1;
    text-decoration: none;
    font-size: 25px;
    font-weight: 800;
}
.header a:hover {
    color: #efeff1;
    text-shadow:
      0 0 1px #efeff1,
      0 0 2px #efeff1,
      0 0 5px #efeff1,
      0 0 7px #EA3333,
      0 0 11px #EA3333,
      0 0 15px #EA3333,
      0 0 20px #EA3333;
}
.header h1{
    font-size: 35px;
    font-weight: 800;
}
.header ul{
    display: flex;
    text-align: center;
    align-items: center;
}

nav{
    height: 100dvh;
    border-right: 2px solid rgba(126, 125, 125, 0.677);
}

.video__player{
    display: flex;
    margin: 0px auto;

}

.container__player{
    display: flex;
}

.choose__container{
    margin-top: 20px;
    margin-bottom: 20px;
}

.choose__button{
    width: 145px;
    height: 50px;
    background:#18181b;
    border-radius: 5px;
    border-color: rgba(126, 125, 125, 0.677);
    color: aliceblue;
    border-style: solid;
    border-width: 3px;
    font-weight: 800;
    
}

.choose__button:hover {
    border-color:#ffffffb3;
    color: #fff;
    box-shadow: 0 0 0.5em 0.3em #EA3333;
    }

.choose__container{
    margin-left: auto;
    margin-right: auto;
    text-align: center 

}
.wrapper{
    max-width: 100vmax;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

@media all and (min-width: 1024px) and (max-width: 1280px) { 
    :root {
        --main-width: 1280px;
      }
    .header a{
        color: #efeff1;
        text-decoration: none;
        font-size: 20px;
        font-weight: 800;
    }

    
}

@media all and (min-width: 768px) and (max-width: 1024px) {
    :root {
        --main-width: 1024;
      }
    .header a{
        color: #efeff1;
        text-decoration: none;
        font-size: 20px;
        font-weight: 800;
    }
 }

@media all and (min-width: 480px) and (max-width: 768px) {
    :root {
        --main-width: 768px;
      }
      .header a{
        color: #efeff1;
        text-decoration: none;
        font-size: 15px;
        font-weight: 800;
    }

    .header li {
        margin: 0px 30px 0px 0px;
    }
 }

@media all and (max-width: 480px) { 
    :root {
        --main-width: 480px;
      }
      .header a{
        color: #efeff1;
        text-decoration: none;
        font-size: 15px;
        font-weight: 800;
    }
    .header li {
        margin: 0px 20px 0px 0px;
    }
    .choose__button{
        width: 114px;
        height: 50px;
        border-style: solid;
        border-width: 3px;
        font-weight: 800;
        font-size: 9px;
    }
    iframe{
        width: 390px;
        height: 300px;
    }

}

@media all and (max-width: 380px) { 
    :root {
        --main-width: 480px;
      }
      .header a{
        color: #efeff1;
        text-decoration: none;
        font-size: 15px;
        font-weight: 800;
    }
    iframe{
        width: 290px;
        height: 200px;
    }

}

@media all and (max-width: 320px) { 
    :root {
        --main-width: 480px;
      }
      .header a{
        color: #efeff1;
        text-decoration: none;
        font-size: 15px;
        font-weight: 800;
    }
}

@media all and (max-width: 200px) { 
    :root {
        --main-width: 150px;
      }
      .header a{
        color: #efeff1;
        text-decoration: none;
        font-size: 5px;
        font-weight: 800;
        display: wrap;
    }

      iframe{
        width: 150px;
        height: 90px;
    }
}