body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #1f1f23;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.pomodoro {
  color: #c9ccea;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  min-height: 50vh;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.pomodoro__container{
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;


}

.pomodoro h1 {
  font-size:2.6rem;
  margin-bottom:0.2rem;
}

.pomodoro .labels {
  list-style: none;
  display: flex;
  text-transform: uppercase;
  font-size: .8rem;
  padding:.3rem .3rem;
  background-color: #18181b;
  border-radius: 1.5rem;
}


.active-label {
  color: #18181b;
  background-color: #EA3333;
}

.timer-container {
  display: flex;
  flex:2;
  align-items: center;
  justify-content: center;
}

.time-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15rem;
  width: 15rem;
  border-radius: 7.5rem;
  color:#efefef;
  background: #151932;
  font-size: 3.5rem;
  box-shadow: 1rem 1.5rem 2rem rgba(0,0,0,0.6)
}

.button-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding:2rem
}

button {
  color:#efefef;
  font-size:0.8rem;
  padding: 0.5rem 1.2rem;
  border:none;
  border-radius:3rem;
  margin:0.2rem;;
  background:#0C0E1B;
  min-width: 80px;
  cursor: pointer;
}

button:hover {
  border-color:#ffffffb3;
  color: #fff;
  box-shadow: 0 0 0.5em 0.3em #EA3333;
  }

.button-wrapper button.activve {
  color: #fff;
  background-color: #C9CCEA;
  border: 1px solid #fff;
}
.form-container {
  text-align: center;
}
.input-wrapper {
  margin: 1.5rem;
}
.input-wrapper input {
  height: 30px;
  width: 30px;
  padding: 2rem;
  background-color: #0C0E1B;
  color:#C9CCEA;
  border: none;
  border-radius: 50%;
  margin-right: 0.3rem;
  text-align: center;
  font-size: 14pt;
}
